'use strict'

const Constants = require('../../../../webconvert/lib/src/constants');
const TryCatch = new (require('../../../../Excel/src/utils/try-catch'));
const message = require('../../../../Excel/src/utils/message');
const funcUtils = require('../../../../webconvert/lib/utils/func-utils');
const ExcelJS = require('../../../../Excel/src/exceljs/exceljs.browser');
// let ExcelJS;
require('../../../../webconvert/lib/src/xlsx/xlsx');

let $html = {};
let workbook;
const copyModel = {modelCopied: false, fileRead: false, workbookFrom: undefined, reader: undefined,
    files: undefined, externalWorkbook: undefined, fileSuffix: '_NEW'};

Office.onReady( async (info) => {
    if (info.host === Office.HostType.Word) {

        // ExcelJS = window.ExcelJS;
        // ExcelJS.Workbook = require("../../../../webconvert/lib/src/workbook");
        // ExcelJS.Workbook.Worksheet = require("../../../../webconvert/lib/src/worksheet");
        // ExcelJS.Workbook.Worksheet.Row = require("../../../../webconvert/lib/src/row");
        // ExcelJS.Workbook.Worksheet.Row.Cell = require("../../../../webconvert/lib/src/cell");

        $html = {
            e2wExcelFile: $('#e2w-excel-file'),
            e2wApplyButton: $('#e2w-apply-button'),
            e2wCategories: $('#e2w-categories'),
            option: $('<option></option>'),
        };
        $html.e2wExcelFile.on('change', () => TryCatch.tryCatch(e2wExcelFileSelected));

        await TryCatch.tryCatch(e2wDisplay);

        const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle=\'tooltip\']')
        const tooltipList = [...tooltipTriggerList].map(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl))
        $('.startup').hide();
        $('.main').show();
    }
});

async function e2wDisplay() {
    return true;
}

async function e2wExcelFileSelected() {
    copyModel.files = $html.e2wExcelFile[0].files;
    if (await e2wDisplay()) {
        $html.e2wApplyButton.prop('disabled', true);
        message.alertInfo(`Loading: ${copyModel.files[0].name}`, '.message-alert-excel-file');
        // https://github.com/OfficeDev/office-js-snippets/blob/main/samples/excel/50-workbook/create-workbook.yaml
        copyModel.reader = new FileReader();
        copyModel.reader.onload = () => TryCatch.tryCatch(e2wExcelFileRead);
        // Read in the file as a data URL, and we can parse the Base64-encoded string.
        copyModel.reader.readAsArrayBuffer(copyModel.files[0]);
    }
}

async function e2wExcelFileRead(){
    // Now read in the file
    // // https://github.com/exceljs/exceljs/issues/832
    const buffer = copyModel.reader.result;
    copyModel.workbookFrom = new ExcelJS.Workbook();
    copyModel.workbookFrom.init(copyModel.files[0], true);
    await copyModel.workbookFrom.xlsx.load(buffer);
    console.log(`Read workbook files: ${copyModel.files[0].name}`);
    await copyModel.workbookFrom.loadXml(true);
    // check if any equations exists...
    if (!message.check(copyModel.workbookFrom, {checkIsModel: true}))
        return false;
    copyModel.fileRead = true;
    message.alertInfo(`Loaded: ${copyModel.files[0].name}`, '.message-alert-excel-file');

    e2wCategoriesDisplay();

    $html.e2wExcelFile.prop('disabled', false);
}

function e2wCategoriesDisplay() {
    $html.e2wCategories.html('');
    const options = [];
    for (const sheetTag of Constants.sheetTags.bc) {
        const worksheet = workbook.getWorksheetByTagName(sheetTag);
        if (worksheet) {
            const sections = worksheet.getSectionByTagName(Constants.row.tags.equationDesignator);
            options.push(`${sections.length} ${sheetTag} included in total:`);
            for (const section of sections) {
                const includeInTotals = funcUtils.convertToBoolean(section.start.xmlTableData.getAttribute(Constants.row.attributes.tableData.includeInTotals));
                if (includeInTotals) {
                    const variables = section.getRowsByTag(Constants.row.tags.variableDesignator);
                    const titleRow = section.getRowByTag('Title');
                    let title = '';
                    if (titleRow) {
                        const cd = workbook.activeSheet.getRow(titleRow.number).getColDataByName('title');
                        if (cd) {
                            title = cd.cell.valueOrResult;
                            options.push(`#${options.length + 1}: ${title} (${variables.length} variables)`);
                        }
                    }
                }
            }
        }
    }
    for (const option of options) {
        const $option = $($html.option.clone());
        $option.html(option);
        $html.e2wCategories.append($option);
    }
    $html.e2wCategories.prop('size', options.length);
}


